<template>
    <article>
        <div id="contextBox" contenteditable="true">
            <p><br /></p>
        </div>
    </article>
</template>

<script>
export default {
    mounted() {
        //this.setDef();
    },
    methods: {
        setDef() {
            let contenteditableBox = document.querySelector("#contextBox");
            let contenteditableBoxFirstP = contenteditableBox.firstChild;
            let range = document.createRange(); // Range  -,    .
            let sel = window.getSelection(); //    .     
            range.setStart(contenteditableBoxFirstP, 0); //Node  , Range   .     
            range.collapse(true); //Range ,   .
            sel.removeAllRanges(); //    
            sel.addRange(range); // range   
        },
    },
};
</script>

<style>
#contextBox {
    position: absolute;
    
    
    top: 50%;
    left: 0;
    color: black;
}
</style>
